                                        (a = t(a, '.')),
                                        isNaN(e) || null == e)
                                ) { return 0; }
                                e = (e / 100).toFixed(r);
                                var i = e.split('.'),
                                    o = i[0].replace(/(\d)(?=(\d\d\d)+(?!\d))/g, '$1' + n),
                                    s = i[1] ? a + i[1] : '';
